import { useState } from 'react';
import { useSelector } from 'react-redux';
import styled from 'styled-components';
import { fetchEmailSend } from '../../../apis';

import BackgroundSVG from '../../../assets/images/authModalBackground.svg';
import LoadingSpinner from '../../../assets/styles/loadingSpinner';
import DefaultBtn from '../../../components/button/defaultBtn';
import { rootState } from '../../../store';

const BackgroundIMG = styled.img`
  position: absolute;
  z-index: -2;
`;

const ContentWrapper = styled.div`
  padding: 40px;
  position: relative;
  height: calc(100% - 80px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
`;

const ContentTitle = styled.h2`
  font-size: var(--font-size-large);
  color: #000;
  font-weight: bold;
  text-align: center;
  letter-spacing: 1px;
`;

const ContentTitleWrapper = styled.div``;

const ContentTitleDetail = styled.h2`
  font-size: var(--font-size-mid);
  color: #000;
  font-weight: bold;
  text-align: center;
  letter-spacing: 1px;
`;

const HighlightText = styled.span`
  font-weight: bold;
  color: var(--color-blue);
`;

const SkipBtn = styled.div`
  font-size: var(--font-size-base);
  color: var(--color-dark-grey);
  cursor: pointer;
`;

const FailAuthWrapper = styled.div`
  width: 500px;
  display: flex;
  justify-content: space-between;
`;

const FailAuthTitle = styled.span`
  font-weight: bold;
  font-size: var(--font-size-small);
`;

const ResendBtn = styled.span`
  font-size: var(--font-size-small);
  color: var(--color-blue);
  cursor: pointer;
`;

function EmailAuth({ setNextStep }: { setNextStep: () => void }) {
  const userEmail = useSelector((state: rootState) => state.auth.userId);

  const [loading, setLoading] = useState(false);

  const alertSkipEmailAuth = () => {
    const ok = confirm('      .  ?');
    if (ok) {
      setNextStep();
    }
  };

  const onClickBtn = async () => {
    setLoading(true);
    const response = await fetchEmailSend(userEmail);

    if (response.ok) {
      setNextStep();
    } else {
      alert(response.msg);
    }
  };

  return (
    <>
      <BackgroundIMG src={BackgroundSVG} />
      <ContentWrapper>
        {loading ? <ContentTitle> </ContentTitle> : <ContentTitle> </ContentTitle>}
        {loading ? (
          <LoadingSpinner />
        ) : (
          <>
            <ContentTitleWrapper>
              <ContentTitleDetail>     </ContentTitleDetail>
              <ContentTitleDetail>
                <HighlightText> </HighlightText>   .
              </ContentTitleDetail>
            </ContentTitleWrapper>
            <DefaultBtn onClick={onClickBtn} btnName={' '} width={500} height={50} color={'blue'} />
            <SkipBtn onClick={alertSkipEmailAuth}></SkipBtn>
          </>
        )}
        <FailAuthWrapper>
          <FailAuthTitle>  ?</FailAuthTitle>
          <ResendBtn>  </ResendBtn>
        </FailAuthWrapper>
      </ContentWrapper>
    </>
  );
}
export default EmailAuth;
